.openModalExtensionRadioСontainer{
  border: 2px solid white;
  width: 450px;
  padding: 20px;
  margin-bottom: 20px;
  font-size: 18px;
  outline: none;
}

.openModalExtensionRadioСontainer h2{
  margin-bottom: 10px;
}

.openModalExtensionRadio {
  padding-bottom: 10px;
  /* border: 2px solid #fff;
  border-radius: 6px; */
}

.openModalExtensionRadio input[type=radio] {
	display: none;
}

.openModalExtensionRadio label {
	cursor: pointer;
	padding: 0px 15px;
	line-height: 34px;
	border: 2px solid #fff;
	border-radius: 6px;
	user-select: none;
}

.openModalExtensionRadio input[type=radio]:checked + label {
	background: var(--accent);
}

.openModalExtensionRadio label:hover {
	color: #fff;
}
 
